import React, { useState } from 'react';
import styles from './TaskManager.module.css';

interface Task {
  id: string;
  title: string;
  type: 'habit' | 'daily' | 'todo';
  completed?: boolean;
}

export default function TaskManager() {
  const [activeTab, setActiveTab] = useState<'habits' | 'dailies' | 'todos'>('habits');
  const [tasks, setTasks] = useState<Task[]>([]);
  const [searchQuery, setSearchQuery] = useState('');

  const addTask = (type: 'habit' | 'daily' | 'todo') => {
    const newTask: Task = {
      id: Math.random().toString(36).substr(2, 9),
      title: '',
      type: type,
      completed: false,
    };
    setTasks([...tasks, newTask]);
  };

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <input
          type="text"
          placeholder="Search"
          className={styles.searchInput}
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
        />
        <button 
          className={styles.addTaskButton}
          onClick={() => addTask(activeTab.slice(0, -1) as 'habit' | 'daily' | 'todo')}
        >
          + Add Task
        </button>
      </div>

      <div className={styles.tabs}>
        <button
          className={`${styles.tab} ${activeTab === 'habits' ? styles.active : ''}`}
          onClick={() => setActiveTab('habits')}
        >
          Habits
        </button>
        <button
          className={`${styles.tab} ${activeTab === 'dailies' ? styles.active : ''}`}
          onClick={() => setActiveTab('dailies')}
        >
          Dailies
        </button>
        <button
          className={`${styles.tab} ${activeTab === 'todos' ? styles.active : ''}`}
          onClick={() => setActiveTab('todos')}
        >
          To Dos
        </button>
      </div>

      <div className={styles.content}>
        <div className={styles.section}>
          <div className={styles.sectionHeader}>
            {activeTab === 'habits' && 'Add a Habit'}
            {activeTab === 'dailies' && 'Add a Daily'}
            {activeTab === 'todos' && 'Add a To Do'}
          </div>
          
          <div className={styles.taskList}>
            {tasks
              .filter(task => task.type === activeTab.slice(0, -1))
              .map(task => (
                <div key={task.id} className={styles.taskItem}>
                  <div className={styles.taskContent}>
                    {task.title || 'Click here to edit this task'}
                  </div>
                  <div className={styles.taskActions}>
                    <button className={styles.deleteButton}>‚àí</button>
                  </div>
                </div>
              ))}
            
            {tasks.filter(task => task.type === activeTab.slice(0, -1)).length === 0 && (
              <div className={styles.emptyState}>
                <div className={styles.emptyIcon}>
                  {activeTab === 'habits' && '‚öñÔ∏è'}
                  {activeTab === 'dailies' && 'üìÖ'}
                  {activeTab === 'todos' && '‚úì'}
                </div>
                <div className={styles.emptyText}>
                  {`These are your ${activeTab}`}
                  <br />
                  {activeTab === 'habits' && "Habits don't have a rigid schedule. You can check them off multiple times per day."}
                  {activeTab === 'dailies' && 'Dailies repeat on a regular basis. Choose the schedule that works best for you!'}
                  {activeTab === 'todos' && 'To Dos need to be completed once. Add checklists to your To Dos to increase their value.'}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
} 